import React from 'react';

import './detail.css';
import { isArray, isObject } from 'util';
import drawShells from './electronShells.js';


class ElementDetail extends React.Component{
    constructor(props){
        super(props);
        this.state = {

        };
        this.perShell = [];
    }

    componentDidMount(){
        // 组件挂载完成
        let perShell =this.perShell[0].split(',')
        console.log('perShell ->',perShell)
        drawShells('electron-shells',perShell);
    }

    imageCard(cardData){
        let cardImage;
        try{
            cardImage = require(`../../file/elements/${cardData[0]}.jpg`);
        }catch(e){
            cardImage = '';
        }
        var imageCard = (
            <div className="detail-image-card-wrapper-wrapper">
                <div className="detail-image-card-wrapper" style={{'background-color':this.props.color[cardData[6]]}}>
                    {cardImage ? <img className="detail-image-card-iamge" src={cardImage}></img> : ''}
                    <div className="detail-image-card-filter" style={{'border-bottom':'2px solid #e9db39'}}></div>
                    <div className="detail-image-card-info-wrapper">
                        <span className="detail-image-card-symbol">{cardData[0]}</span>
                        <span className="detail-image-card-zh">{cardData[1]}</span>
                        <span className="detail-image-card-number">{cardData[4]}号</span>
                        <span className="detail-image-card-en">{cardData[2]}</span>
                        <span className="detail-image-card-atomic-mass">{cardData[5]}</span>
                    </div>
                </div>
            </div>
        )
    
        return imageCard;
    }

    detailInfo(unitData){
        // unitData [[],[],]
        let item = [];
        // console.log('detail info ->',unitData)
        for(let i=0; i<unitData.data.length; i++){
            // console.log(unitData.data[i])
            if(Object.prototype.toString.call(unitData.data[i]) == "[object Array]"){
                item.push((
                    <div className="unit-item-box-wrapper">
                        <span className="unit-item-title">{unitData.data[i][0]}</span>
                        <span className="unit-item-data">{unitData.data[i][1]}</span>
                    </div>
                ))
            // }else if(unitData.data == undefined || unitData == undefined){
            //         continue
            }else if(unitData.data[i].name == "atomStructure"){
                item.push(
                    this.electronShells(unitData.data[i].data)
                );
            }else if(unitData.data[i].name == "spectralblock"){
                item.push(
                    this.spectral()
                );
            }else{
                console.log('未处理跳出->',unitData.data[i])
            }
        }
    
        var unit = (
            <div className="">
                <div className="unit-title-box-wrapper">
                    <div className="unit-title-icon-wrapper">
                        <img className="unit-title-fold-icon" src={require('../../file/icon/fold_l_b.png')}></img>
                        <span>{unitData.title}</span>
                    </div>
                    <img className="unit-title-cion" src={require('../../file/icon/原子性质_b.png')}></img>
                </div>
                {item}
            </div>
        )
        return unit;
    }


    spectral(){
        let elementNumber = this.props.detailData[0].data[0]
        let spectralImg;
        console.log('spectral ->',elementNumber)
        try{
            spectralImg = require(`../../file/spectral/${elementNumber}.png`);
        }catch(e){
            spectralImg = '';
        }
        var spectral = (
            <div className="detail-element-spectral-wrapper">
                <span>原子光谱</span>
                <img className="detail-element-spectral-iamge" src={spectralImg}></img>
            </div>
        )
        return spectral;
    }
    

    electronShells(data){
        this.perShell = data;
        return (
            <div className="detail-electron-shells-wrapper">
                <span>电子层</span>
                <canvas id="electron-shells"></canvas>
            </div>
        )
    }

    render(){
        let detail = [];
        // console.log('detail ->',this.props.detailData)
        for(let i=0; i<this.props.detailData.length;i++){
            if(this.props.detailData[i].is == "unit"){
                // console.log('else ->', this.props.detailData[i])
                detail.push(
                    this.detailInfo(this.props.detailData[i])
                );
            }else if(this.props.detailData[i].is == "imageCard"){
                // console.log('else ->', this.props.detailData[i])
                detail.push(
                    this.imageCard(this.props.detailData[i].data)
                );
            }else{
                console.log('End else ->', this.props.detailData[i])
            }
        }

        // console.log('detail ->->',detail)

        return(
            <div className="detail-wrapper-wrapper"
                onClick={()=>this.props.handleClickMasking()}
            >
                <div className="detail-wrapper"
                    onClick={(e)=>e.stopPropagation()}
                >
                    {detail}
                </div>
                <div className="detail-close-wrapper">
                    <div className="detail-close">×</div>
                </div>
            </div>
        )
    }
}

export default ElementDetail;